import React from 'react';
import axios from 'axios';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import initFontAwesome from "./initFontAwesome";

import './App.css';
initFontAwesome();

function App() {
  
  let [responseData, setResponseData] = React.useState('');

  const fetchData = React.useCallback(() => {
    axios({
      "method": "GET",
      "url": "https://quotes15.p.rapidapi.com/quotes/random/",
      "headers": {
        "content-type": "application/octet-stream",
        "x-rapidapi-host": "quotes15.p.rapidapi.com",
        "x-rapidapi-key": process.env.REACT_APP_API_KEY
      }, "params": {
        "language_code": "en"
      }
    })
    .then((response) => {
      setResponseData(response.data)
    })
    .catch((error) => {
      console.log(error)
    })
  }, [])

  React.useEffect(() => {
    fetchData()
  }, [fetchData])

  React.useEffect(() => {
    const script = document.createElement('script');
    script.src = "https://cdn.freecodecamp.org/testable-projects-fcc/v1/bundle.js";
    script.async = true;
    document.body.appendChild(script);
  return () => {
      document.body.removeChild(script);
    }
  }, []);

  return (
    <div className="App" id="quote-box">
      <header className="App-header">
        <h1>
          Fetching quotes API with React Hooks
        </h1>
        <a id="tweet-quote" target="blank" href="https://twitter.com/intent/tweet?" data-text="custom share text">
        <FontAwesomeIcon icon={['fab', 'twitter']} size="lg"/>
        </a>
        <p>TWEET ME!</p>
        <button id="new-quote" type='button' onClick={fetchData}>Fetch a quote</button>
      </header>
      {/* <pre>
        <code>
          {responseData && JSON.stringify(responseData, null, 4)}
        </code>
      </pre> */}
      <main>
        {responseData &&
          <blockquote id="text">
            "{responseData && responseData.content}"
            <small id="author">{responseData && responseData.originator && responseData.originator.name}</small>
          </blockquote>}
      </main>
    </div>
  );
}

export default App;
